:fullscreen {
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
}

:fullscreen .magnify-header,
:fullscreen .magnify-footer,
:fullscreen .magnify-resizable-handle {
  display: none;
}

:fullscreen .magnify-stage {
  top: 0;
