}

::backdrop {
  background-color: #000;
}

::-ms-backdrop {
  background-color: #000;
}

.is-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.is-grabbing {
  cursor: move;
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
